$accent: #b77cb9;
$tinted: 200deg;

// generate our variables
@import "../import";

// define filters
// to apply color shifts
// to images / elements
// ( off - opacity: 1 )
%vote-filter {
	@include shift($tinted, 1, 0.8); }

%room-filter {
	@include gloss($tinted + 80deg, 2); }

%main-filter {
	@include paint($tinted + 50deg, 2, 0.55); }

%hard-filter {
	@include paint($tinted + 50deg, 2, 0.55, 1); }

%mute-filter {
	@include paint($tinted + 70deg, 1.5, 0.4); }

@import "engine/extends";
@import "engine/colorize";
